import { formatNumber } from '@cetus/utils'
import { HStack, Progress, Text } from '@chakra-ui/react'

export default function DcaProgressBlock({ orderInfo, isProfile }: { orderInfo: any; isProfile?: boolean }) {
  const { inCoin: sellCoin } = orderInfo
  return (
    <HStack flexDirection={isProfile ? 'column' : 'row'} align={isProfile ? 'flex-end' : 'center'} gap={isProfile ? '4px' : '8px'}>
      <HStack>
        <Text color="text_caption" fontSize={isProfile ? '14px' : '12px'}>
          {formatNumber(orderInfo?.inDepositedOut)} {sellCoin?.symbol}
        </Text>
        <Text color={isProfile ? 'text_caption' : 'primary_gray'} fontSize={isProfile ? '14px' : '12px'}>
          / {formatNumber(orderInfo?.inDeposited)} {sellCoin?.symbol}
        </Text>
      </HStack>
      <HStack>
        <Text color="primary_gray" fontSize="12px">
          ({orderInfo?.inBalanceRatio}%)
        </Text>
        <Progress
          w={isProfile ? '40px' : '50px'}
          h="4px"
          value={orderInfo?.inBalanceRatio}
          bg="#282828"
          sx={{
            'div[role="progressbar"]': {
              bg: 'primary'
            }
          }}
        />
      </HStack>
    </HStack>
  )
}
